*{  
    /* We are doing this as we want our navbar to cover the edge of the browser. */
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: Inter, sans-serif;
    background-color: #282D35;
}

nav {
    display: flex;
    align-items: center;
    background-color: #21222A;
    height: 90px;
    padding: 30px 25px;
}

.nav--logo_text, .nav--title {
    margin: 0;
}

.nav--logo_text {
    margin-right: auto;
    color: #61DAFB;
    font-weight: 700;
    font-size: 22px;
}

.nav--title {
    color: #DEEBF8;
    font-weight: 600;
}

.nav--icon {
    height: 30px;
    margin-right: 7px;
}

main{
    padding: 55px;
    color: #FFFFFF;
    background-image: url("./back-logo.png");
    background-repeat: no-repeat;
    background-position:right bottom;
    background-size: 200px;

    
}

.main--title{
    
    margin: 0;
    font-size: 39.06px;
    font-style: normal;
    font-weight: 700;
    line-height: 47px;
    letter-spacing: -0.05em;

}

.main--facts {
    margin-top: 46px;
    /* max-width: 400px; */
}

.main--facts > li {
    line-height: 19px;
    padding-block: 10px;
}

.main--facts > li::marker {
    font-size: 1.4rem;
    color: #61DAFB;
}